import { ListItemSecondaryAction } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import EDU_AXIOS from 'src/helpers/edu_axios';
import notify from 'src/helpers/notify';
import Checksections from './checksections';

export default function Sections({className,school_id,newsections,teacher_id,subject_name}) {
    let [sections, setSections] = useState([]);
    let [disabled, setDisabled] = useState(true);
   let data=[];
   
    useEffect(() => {
        EDU_AXIOS.get(
            `${process.env.REACT_APP_API_PATH}/cm/get-sections-by-class/${school_id}/${className}`
          )
            .then((response) => {
              
              setSections(response.data.data);
             data.push(sections);
          //   console.log(newsections)
              // console.log("data")
              // console.log(newsections)
            })
            .catch((error) => {
            
              console.log(error);
            });
    })

    const changesectionchecbox=async(val,event,index)=>{
  setDisabled(false)
        if(event.target.checked==false){
       for(var i=0;i<newsections.length;i++){
           //alert(newsections[i])
           if(newsections[i]==val){
               newsections.splice(i,1);
               break;
           }
       }
    }
    else{
        newsections.push(val);
    }
        //alert(newsections);
       
    }
    const update = async()=>{
      if(select_all==true){
        console.log("select all is true")
        console.log(sections);
        newsections=[];
        for(var i=0;i<sections.length;i++){
               newsections.push(sections[i].section_name);
        }
      }
     // alert('ok')
     if(newsections!='')
     {
      await EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/cm/class-mapping", {
        school_id: school_id,
        teacher_id: teacher_id,
        c_class: className,
        c_subject: subject_name,
        sections: newsections,
      })
        .then(async (res) => {
        //   resetForm({ values: "" });
          notify("success", res.data.message);
          setDisabled(true);
        //   setVisible(false);
        //   getDetail(classNames, teacher_id);
      //  alert("data updated successfuly");
        })
        .catch((err) => {
          notify("error", err);
        });
      }
      else{
        await EDU_AXIOS.post(process.env.REACT_APP_API_PATH + "/cm/class-mapping", {
          school_id: school_id,
          teacher_id: teacher_id,
          c_class: className,
          c_subject: subject_name,
          sections: sections,
        })
          .then(async (res) => {
          //   resetForm({ values: "" });
            notify("success", res.data.message);
            setDisabled(true);
          //   setVisible(false);
          //   getDetail(classNames, teacher_id);
        //  alert("data updated successfuly");
          })
          .catch((err) => {
            notify("error", err);
          });
      }
    }

    const [select_all, setSelectall] = useState(false)
    const selectall = ()=>{
           setSelectall(true);
           setDisabled(false);
    }


  return (
    <>
      <input type="checkbox" onChange={()=>selectall()} />   Select All &nbsp;
                    
    {

        sections.map((item,index)=>(
         //start

         newsections==''?  <>
         {
           select_all==true? <input type="checkbox" value={item.section_name} onChange={(event)=>changesectionchecbox(event.target.value,event,index)} checked></input>
        :<input type="checkbox" value={item.section_name} onChange={(event)=>changesectionchecbox(event.target.value,event,index)}>
        </input>
         }  
        
          
          <label>{item.section_name}</label>
          </>
          :

            newsections.find(element => element==item.section_name || select_all==true)?
         <>
          
           <input type="checkbox" value={item.section_name} onChange={(event)=>changesectionchecbox(event.target.value,event,index)} checked>
          
           </input>
           
           <label>{item.section_name}</label>
         </>
           :
           <>
           
            <input type="checkbox" value={item.section_name} onChange={(event)=>changesectionchecbox(event.target.value,event,index)}>
           
             </input> 
             <label>{item.section_name}</label>
             </>
          
    // end
         
        ))
       
        
    }
    {
     
 <button onClick={update} style={{marginLeft:" 194px",
  position: "absolute"}} disabled={disabled}>Update</button>
    }
   
 {/* {
     sections.map((item)=>{
     return    <p>{item.section_name}</p>
     })
 } */}
    </>
  );
}
